body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
}

body{
  background-color: #0A0B1A;overflow-x: hidden;
}

li, a{
  text-decoration: none;
  list-style: none;
  color: inherit;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


/* navbar */
nav{
  width: 100%;
  position: fixed;
  z-index: 1;
}

.navbar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: .5rem;
  margin-right: 5rem;
  margin-left: 10rem;
}


.nav-list{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

nav li{
  color: #fff;
  font-family: 'Open Sans', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 26px;
  margin: 0 1rem;
}

.fa{
  color: #fff;
}

.humburger,
.fa-times{
  display: none;
}

@media only screen and (max-width: 500px){
  .navbar {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-top: .5rem;
    margin-right: 1rem;
    margin-left: 1rem;
  }
  
  nav li{
    margin: 0 .4rem;
  }
  
  .nav-list span{
    display: none;
  }
  
  .menu-toggle{
    margin-left: 1.3rem;
  }

  .humburger{
    display: block;
  }
}

/* Top-banner */
.home {
  position: relative;
  /* overflow: hidden; */
  height: 100vh;
  background:linear-gradient(180deg, rgba(5, 11, 20, 0.28) 0%, rgba(1, 13, 32, 0.69) 67.53%, #0A0B1A 85.33%), url(/home_jam/static/media/Rectangle-157.730645c7.png);
}

.home h1{
  position: absolute;
  font-size: 5rem;
  top: 15rem;
  left: 10rem;
  font-family: 'Libre Baskerville', serif;
  color: #fff;
  font-size: 80px;
}

.home p{
  position: absolute;
  width: 40rem;
  top: 23rem;
  left: 10rem;
  color: #fff;
  font-family: 'Nunito', sans-serif;
  font-size: 24px;
  opacity: 0.6;
}

@media only screen and (max-width: 500px){
  .home {
    background:linear-gradient(180deg, rgba(5, 11, 20, 0.28) 0%, rgba(1, 13, 32, 0.69) 67.53%, #0A0B1A 85.33%), url(/home_jam/static/media/Rectangle-M-157.bca20463.png);
  }

  .home h1{
    top: 19rem;
    left: 1rem;
    font-size: 40px;
  }
  
  .home p{
    width: 85%;
    top: 23rem;
    left: 1rem;
    font-size: 16px;
  }
}
